$background-color: #aaa;
$background-color: #eee;

body, html {
    background-color: $background-color;
}


#content {
  // margin-top: 150px;
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  width: 100%;
  height: 100%;

  // opacity: 0;
  // visibility: hidden;

  #background, canvas {
    width: 100%;
    height: 100%;
  }

  canvas {
    background-color: $background-color;
  }
}

.main {
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  .text {
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%,-50%);

    color: white;
    font-size: 2em;
  }
}



